.friends-list {
    width: 300px;
    margin-bottom: 30px;
}

.item {
    display: flex;
    align-items: center;
    border-radius: 6px;
    padding: 13px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    box-shadow: 0px 0px 5px rgba(33, 33, 33, 0.2);
    margin-top: 10px;
}

.status {
    border-radius: 50%;
    width: 15px;
    height: 15px;
    margin-right: 15px;
    display: inline-block;
}

.avatar {
    width: 48px;
    margin-right: 15px;
}

.label{
    font-size: 20px;
    font-weight: 600;
    color: #333;
}