.transaction-history{
    width: 900px;
}

.heading{
    padding: 16px 0;
}

.title{
    padding: 16px 0;
    font-size: 20px;
    text-align: center;
    color: #fff;
    background-color: #00bcd5;
    
}

.element{
    background-color: #fff;
}

.element:nth-child(3n+2) {
    background-color: #ecf1f3;
}

.item{
    padding: 16px 0;
    text-align: center;
    border: 1px solid rgba(33, 33, 33, 0.2);
    box-shadow: 0px 0px 5px #ccc;
}

