.statistics{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    background-color: #f5f5f5;
    margin-bottom: 30px;
}

.title{
    margin: 20px 0;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
}

.stat-list{
    display: flex;
}

.item{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    border-radius: 6px;
    border: 1px solid #ccc;
    padding: 10px;
    color: #fff;
}

.item:nth-child(1){
    background-color: #54c2f5;
}

.item:nth-child(2) {
    background-color: #a43cf4;
}

.item:nth-child(3) {
    background-color: #e64c66;
}

.item:nth-child(4) {
    background-color: #20b8c5;
}

.item:nth-child(5) {
    background-color: #e28743;
}

.label{
    font-size: 16px;
}

.percentage{
    font-size: 18px;
    font-weight: 500;
}